













var alertTimer = null;
import graceAlert from '../../graceUI/components/graceAlert.vue';
export default {
	data() {
		return {
			show : false,
			msg : '',
			msgtype : ''
		}
	},
	methods: {
		showMsg1 : function(){
			this.show    = true;
			this.msgtype = "right";
			this.msg     = "操作成功 ^_^";
			//延迟关闭
			var _self = this;
			if (alertTimer != null) { clearTimeout(alertTimer);}
			alertTimer = setTimeout(function(){_self.show = false; _self.msg = '';}, 2000);
		},
		showMsg2: function () {
			this.show    = true;
			this.msgtype = "error";
			this.msg     = "操作失败!";
			//延迟关闭
			var _self = this;
			if (alertTimer != null) { clearTimeout(alertTimer); }
			alertTimer = setTimeout(function(){_self.show = false; _self.msg = '';}, 2000);
		},
		showMsg3: function () {
			this.show    = true;
			this.msgtype = "msg";
			this.msg     = "Hi! I am GraceUI alert ^_^";
			//延迟关闭
			var _self = this;
			if (alertTimer != null) { clearTimeout(alertTimer); }
			alertTimer = setTimeout(function(){_self.show = false; _self.msg = '';}, 2000);
		}
	},
	components : {
		graceAlert
	}
}
